* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

p {
  margin: 0;
}

ul {
  padding: 0;
}

.list {
  padding-left: 0px;
  margin: 0;
  list-style-type: none;
}

.country-list__item {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  padding-bottom: 20px;
}

.country-list__item:last-child {
  padding-bottom: 0;
}

.country-list__text {
  padding-left: 10px;
}

.country-info__item:first-child {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
}

.country-info__img {
  padding-right: 10px;
}